"use client";
import React, { useState } from "react";
import { Button } from "@/components/ui/button";

const DestinationBox = () => {
  const [pickupLocation, setPickupLocation] = useState("");
  const [dropoffLocation, setDropoffLocation] = useState("");
  const [pickupDate, setPickupDate] = useState("");
  const [dropoffDate, setDropoffDate] = useState("");
  const [pickupTime, setPickupTime] = useState("");
  const [dropoffTime, setDropoffTime] = useState("");

  const handlePickupChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setPickupLocation(event.target.value);
  };

  const handleDropoffChange = (event: React.ChangeEvent<HTMLInputElement>) => {
    setDropoffLocation(event.target.value);
  };

  const handleSubmit = (event: React.FormEvent) => {
    event.preventDefault();
    // Here you could send the form data to a backend or use it in your app
    console.log("Form submitted");
  };

  return (
    <div className="bg-white p-8 rounded-lg shadow-lg w-full max-w-4xl">
      <form onSubmit={handleSubmit} className="grid grid-cols-1 md:grid-cols-2 gap-6">
        <div className="flex flex-col">
          <label htmlFor="pickup" className="text-sm font-medium text-gray-700">
            Pickup Location
          </label>
          <input
            type="text"
            id="pickup"
            value={pickupLocation}
            onChange={handlePickupChange}
            placeholder="Enter pickup location"
            className="mt-1 p-2 border border-gray-300 rounded-md"
          />
        </div>

        <div className="flex flex-col">
          <label htmlFor="dropoff" className="text-sm font-medium text-gray-700">
            Drop-off Location
          </label>
          <input
            type="text"
            id="dropoff"
            value={dropoffLocation}
            onChange={handleDropoffChange}
            placeholder="Enter drop-off location"
            className="mt-1 p-2 border border-gray-300 rounded-md"
          />
        </div>

        <div className="flex flex-col">
          <label htmlFor="pickupDate" className="text-sm font-medium text-gray-700">
            Pickup Date
          </label>
          <input
            type="date"
            id="pickupDate"
            value={pickupDate}
            onChange={(e) => setPickupDate(e.target.value)}
            className="mt-1 p-2 border border-gray-300 rounded-md"
          />
        </div>

        <div className="flex flex-col">
          <label htmlFor="pickupTime" className="text-sm font-medium text-gray-700">
            Pickup Time
          </label>
          <input
            type="time"
            id="pickupTime"
            value={pickupTime}
            onChange={(e) => setPickupTime(e.target.value)}
            className="mt-1 p-2 border border-gray-300 rounded-md"
          />
        </div>

        <div className="flex flex-col">
          <label htmlFor="dropoffDate" className="text-sm font-medium text-gray-700">
            Drop-off Date
          </label>
          <input
            type="date"
            id="dropoffDate"
            value={dropoffDate}
            onChange={(e) => setDropoffDate(e.target.value)}
            className="mt-1 p-2 border border-gray-300 rounded-md"
          />
        </div>

        <div className="flex flex-col">
          <label htmlFor="dropoffTime" className="text-sm font-medium text-gray-700">
            Drop-off Time
          </label>
          <input
            type="time"
            id="dropoffTime"
            value={dropoffTime}
            onChange={(e) => setDropoffTime(e.target.value)}
            className="mt-1 p-2 border border-gray-300 rounded-md"
          />
        </div>

        <div className="md:col-span-2">
          <Button type="submit" className="w-full">
            Get Estimate
          </Button>
        </div>
      </form>

      {/* Debug Box */}
      <div className="mt-6 p-4 border rounded-md bg-gray-100">
        <h3 className="text-lg font-semibold mb-2">Debug Info</h3>
        <p><strong>Pickup Location:</strong> {pickupLocation || "N/A"}</p>
        <p><strong>Drop-off Location:</strong> {dropoffLocation || "N/A"}</p>
        <p><strong>Pickup Date:</strong> {pickupDate || "N/A"}</p>
        <p><strong>Pickup Time:</strong> {pickupTime || "N/A"}</p>
        <p><strong>Drop-off Date:</strong> {dropoffDate || "N/A"}</p>
        <p><strong>Drop-off Time:</strong> {dropoffTime || "N/A"}</p>
      </div>
    </div>
  );
};

export default DestinationBox;
